.pure-button {
    .inline-block;
    line-height: 1.4;
    white-space: nowrap;
    vertical-align: middle;
    text-align: center;
    cursor: pointer;
    -webkit-user-drag: none;
    user-select: none;
    box-sizing: border-box;
    font-family: inherit;
    font-size: 100%;
    padding: 6px 12px;
    color: #444;
    color: rgba(0, 0, 0, 0.80);
    border: 1px solid transparent;
    border-color: #999;
    background-color: #E6E6E6;
    text-decoration: none;
    border-radius: 2px;
}

.pure-button-hover,
.pure-button:hover,
.pure-button:focus {
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#1a000000', GradientType=0);
    background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.05) 40%, rgba(0, 0, 0, 0.10));
}

.pure-button:focus {
    outline: 0;
}

.pure-button-active,
.pure-button:active {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.15) inset, 0 0 6px rgba(0, 0, 0, 0.20) inset;
    border-color: #000 \9;
}

.pure-button[disabled],
.pure-button-disabled,
.pure-button-disabled:hover,
.pure-button-disabled:focus,
.pure-button-disabled:active {
    border: none;
    background-image: none;
    filter: progid:DXImageTransform.Microsoft.gradient(enabled=false);
    filter: alpha(opacity=40);
    opacity: 0.40;
    cursor: not-allowed;
    box-shadow: none;
}

.pure-button-hidden {
    display: none;
}

/* Firefox: Get rid of the inner focus border */
.pure-button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

// 按钮颜色
.pure-button-primary,
.pure-button-selected,
a.pure-button-primary,
a.pure-button-selected {
    background-color: rgb(0, 120, 231);
    border-color: darken(rgb(0, 120, 231), 5%);
    color: #fff;
}

.pure-button-primary,
.button-success,
.button-error,
.button-warning,
.button-secondary {
    color: white;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    &:hover {
        color: white;
    }
}

.button-success {
    background: rgb(28, 184, 65);
    border-color: darken(rgb(28, 184, 65), 5%);
}

.button-error {
    background: rgb(202, 60, 60);
    border-color: darken(rgb(202, 60, 60), 5%);
}

.button-warning {
    background: rgb(223, 117, 20);
    border-color: darken(rgb(223, 117, 20), 5%);
}

.button-secondary {
    background: rgb(66, 184, 221);
    border-color: darken(rgb(66, 184, 221), 5%);
}

// 按钮尺寸
.button-xsmall {
    font-size: 70%;
}

.button-small {
    font-size: 85%;
}

.button-large {
    font-size: 110%;
}

.button-xlarge {
    font-size: 125%;
}


