@charset "utf-8";
/* --------------------------------------------------------------
 * = 通用辅助样式
 * ----------------------------------------------------------- */
.inline-block {
  display: inline-block;
  *display: inline;
  zoom: 1;
}
.close {
  font-size: 20px;
  font-weight: bold;
  line-height: 20px;
  float: right;
  opacity: .2;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  filter: alpha(opacity=20);
}
.close:hover,
.close:focus {
  cursor: pointer;
  text-decoration: none;
  opacity: .4;
  color: #000;
  filter: alpha(opacity=40);
}
button.close {
  padding: 0;
  cursor: pointer;
  border: 0;
  background: transparent;
  -webkit-appearance: none;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.pull-right {
  float: right;
}
.pull-left {
  float: left;
}
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.invisible {
  visibility: hidden;
}
.affix {
  position: fixed;
  top: 0;
}
.fade {
  transition: opacity .15s linear;
  opacity: 0;
}
.in,
.fade.in {
  opacity: 1;
}
.collapse {
  position: relative;
  overflow: hidden;
  height: 0;
  transition: height .35s ease;
}
.collapse.in,
.in {
  height: auto;
}
.dividing {
  padding: 0 5px;
}
@media (max-width: 48em) {
  .only-show-on-desktop {
    display: none !important;
  }
}
.only-show-on-mobile {
  display: none !important;
}
@media (max-width: 48em) {
  .only-show-on-mobile {
    display: block !important;
  }
}
@media (max-width: 48em) {
  .mobile-text-center {
    text-align: center;
  }
}
.img-rounded {
  border-radius: 6px;
}
.img-circle {
  border-radius: 50%;
}
.img-thumbnail {
  display: inline-block;
  max-width: 100%;
  height: auto;
  padding: 4px;
  line-height: 1.42857143;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  transition: all 0.2s ease-in-out;
}
/* --------------------------------------------------------------
 * = reset 文字排版基础样式
 * ----------------------------------------------------------- */
/* 防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */
html {
  color: #333;
  background: #fff;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-rendering: optimizelegibility;
}
/* 如果你的项目仅支持 IE9+ | Chrome | Firefox 等，推荐在 <html> 中添加 .borderbox 这个 class */
html.borderbox *,
html.borderbox *:before,
html.borderbox *:after {
  box-sizing: border-box;
}
/* 内外边距通常让各个浏览器样式的表现位置不同 */
body,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  margin: 0;
  padding: 0;
}
/* 重设 HTML5 标签, IE 需要在 js 中 createElement(TAG) */
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}
/* HTML5 媒体文件跟 img 保持一致 */
audio,
canvas,
video {
  display: inline-block;
}
/* 要注意表单元素并不继承父级 font 的问题 */
body,
.pure-g [class*="pure-u"] {
  font: 300 14px/1.8 PingFang SC, Lantinghei SC, Microsoft Yahei, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}
/* 去掉各Table cell 的边距并让其边重合 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* 去除默认边框 */
fieldset,
img {
  border: 0;
}
/* 块/段落引用 */
blockquote {
  position: relative;
  color: #999;
  font-weight: 400;
  border-left: 1px solid #FA9A55;
  padding-left: 1em;
  margin: 1em 3em 1em 2em;
}
@media only screen and (max-width: 640px) {
  blockquote {
    margin: 1em 0;
  }
}
/* Firefox 以外，元素没有下划线，需添加 */
acronym,
abbr {
  border-bottom: 1px dotted;
  font-variant: normal;
}
/* 添加鼠标问号，进一步确保应用的语义是正确的（要知道，交互他们也有洁癖，如果你不去掉，那得多花点口舌） */
abbr {
  cursor: help;
}
/* 一致的 del 样式 */
del {
  text-decoration: line-through;
}
address,
caption,
cite,
code,
dfn,
em,
th,
var {
  font-style: normal;
  font-weight: 400;
}
/* 去掉列表前的标识, li 会继承，大部分网站通常用列表来很多内容，所以应该当去 */
ul,
ol {
  list-style: none;
}
/* 对齐是排版最重要的因素, 别让什么都居中 */
caption,
th {
  text-align: left;
}
q:before,
q:after {
  content: '';
}
/* 统一上标和下标 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
}
:root sub,
:root sup {
  vertical-align: baseline;
  /* for ie9 and other modern browsers */
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
/* 让链接在 hover 状态下显示下划线 */
a {
  color: #005ca1;
}
a:hover {
  text-decoration: underline;
}
.typo a:hover {
  color: #003055;
  text-decoration: none;
}
/* 默认不显示下划线，保持页面简洁 */
ins,
a {
  text-decoration: none;
}
/* 专名号：虽然 u 已经重回 html5 Draft，但在所有浏览器中都是可以使用的，
 * 要做到更好，向后兼容的话，添加 class="typo-u" 来显示专名号
 * 关于 <u> 标签：http://www.whatwg.org/specs/web-apps/current-work/multipage/text-level-semantics.html#the-u-element
 * 被放弃的是 4，之前一直搞错 http://www.w3.org/TR/html401/appendix/changes.html#idx-deprecated
 * 一篇关于 <u> 标签的很好文章：http://html5doctor.com/u-element/
 */
u,
.typo-u {
  text-decoration: underline;
}
/* 标记，类似于手写的荧光笔的作用 */
mark {
  background: #fffdd1;
  border-bottom: 1px solid #ffedce;
  padding: 2px;
  margin: 0 5px;
}
/* 代码片断 */
pre,
code,
pre tt {
  font-family: Courier, 'Courier New', monospace;
}
pre {
  background: #e7e7e7;
  border: 1px solid #ccc;
  padding: 1em 1.5em;
  display: block;
  -webkit-overflow-scrolling: touch;
}
/* 一致化 horizontal rule */
hr {
  border: none;
  border-bottom: 1px solid #ccc;
  margin-bottom: 0.8em;
  height: 10px;
}
/* 底部印刷体、版本等标记 */
small,
.typo-small,
figcaption {
  font-size: 0.9em;
  color: #666;
}
strong,
b {
  font-weight: bold;
  color: #000;
}
/* 可拖动文件添加拖动手势 */
[draggable] {
  cursor: move;
}
.clearfix:before,
.clearfix:after,
.ui-box:before,
.ui-box:after,
.media:before,
.media:after,
.ui-dlist:before,
.ui-dlist:after,
.ui-tab-v:before,
.ui-tab-v:after {
  content: "";
  display: table;
}
.clearfix:after,
.ui-box:after,
.media:after,
.ui-dlist:after,
.ui-tab-v:after {
  clear: both;
}
.clearfix,
.ui-box,
.media,
.ui-dlist,
.ui-tab-v {
  zoom: 1;
}
/* 强制文本换行 */
.textwrap,
.textwrap td,
.textwrap th {
  word-wrap: break-word;
  word-break: break-all;
}
.textwrap-table {
  table-layout: fixed;
}
/* 提供 serif 版本的字体设置: iOS 下中文自动 fallback 到 sans-serif */
.serif {
  font-family: Palatino, Optima, Georgia, serif;
}
/* 保证块/段落之间的空白隔行 */
.typo p,
.typo pre,
.typo ul,
.typo ol,
.typo dl,
.typo form,
.typo hr,
.typo table,
.typo-p,
.typo-pre,
.typo-ul,
.typo-ol,
.typo-dl,
.typo-form,
.typo-hr,
.typo-table,
blockquote {
  margin-bottom: 1.2em;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: PingFang SC, Verdana, Helvetica Neue, Microsoft Yahei, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans-serif;
  font-weight: 300;
  color: #000;
  line-height: 1.35;
}
/* 标题应该更贴紧内容，并与其他块区分，margin 值要相应做优化 */
.typo h1,
.typo h2,
.typo h3,
.typo h4,
.typo h5,
.typo h6,
.typo-h1,
.typo-h2,
.typo-h3,
.typo-h4,
.typo-h5,
.typo-h6 {
  margin-top: 1.2em;
  margin-bottom: 0.6em;
  line-height: 1.35;
}
.typo h1,
.typo-h1 {
  font-size: 2em;
}
.typo h2,
.typo-h2 {
  font-size: 1.8em;
}
.typo h3,
.typo-h3 {
  font-size: 1.6em;
}
.typo h4,
.typo-h4 {
  font-size: 1.4em;
}
.typo h5,
.typo h6,
.typo-h5,
.typo-h6 {
  font-size: 1.2em;
}
/* 在文章中，应该还原 ul 和 ol 的样式 */
.typo ul,
.typo-ul {
  margin-left: 1.3em;
  list-style: disc;
}
.typo ol,
.typo-ol {
  list-style: decimal;
  margin-left: 1.9em;
}
.typo li ul,
.typo li ol,
.typo-ul ul,
.typo-ul ol,
.typo-ol ul,
.typo-ol ol {
  margin-bottom: 0.8em;
  margin-left: 2em;
}
.typo li ul,
.typo-ul ul,
.typo-ol ul {
  list-style: circle;
}
/* 同 ul/ol，在文章中应用 table 基本格式 */
.typo table th,
.typo table td,
.typo-table th,
.typo-table td,
.typo table caption {
  border: 1px solid #ccc;
  padding: 0.5em 1em;
  color: #666;
}
.typo table th,
.typo-table th {
  background: #e7e7e7;
}
.typo table thead th,
.typo-table thead th {
  background: #cecece;
}
.typo table caption {
  border-bottom: none;
}
/* 去除 webkit 中 input 和 textarea 的默认样式  */
.typo-input,
.typo-textarea {
  -webkit-appearance: none;
  border-radius: 0;
}
.typo-em,
.typo em,
legend,
caption {
  color: #000;
  font-weight: inherit;
}
/* Responsive images */
.typo img {
  max-width: 100%;
}
/* --------------------------------------------------------------
 * = Pure 栅格系统
 * ----------------------------------------------------------- */
.pure-g {
  letter-spacing: -0.31em;
  /* Webkit: collapse white-space between units */
  *letter-spacing: normal;
  /* reset IE < 8 */
  *word-spacing: -0.43em;
  /* IE < 8: collapse white-space between units */
  text-rendering: optimizespeed;
  /* Webkit: fixes text-rendering: optimizeLegibility */
  font-family: FreeSans, Arimo, "Droid Sans", Helvetica, Arial, sans-serif;
  -webkit-flex-flow: row wrap;
  /* IE10 uses display: flexbox */
  -ms-flex-flow: row wrap;
  /* Prevents distributing space between rows */
  -ms-align-content: flex-start;
  -webkit-align-content: flex-start;
  align-content: flex-start;
}
/* Opera as of 12 on Windows needs word-spacing.
   The ".opera-only" selector is used to prevent actual prefocus styling
   and is not required in markup.
*/
.opera-only :-o-prefocus,
.pure-g {
  word-spacing: -0.43em;
}
.pure-u {
  display: inline-block;
  *display: inline;
  /* IE < 8: fake inline-block */
  zoom: 1;
  letter-spacing: normal;
  word-spacing: normal;
  vertical-align: top;
  text-rendering: auto;
}
/*
Resets the font family back to the OS/browser's default sans-serif font,
this the same font stack that Normalize.css sets for the `body`.
