}
.ui-box-foot-left {
  display: inline-block;
  *display: inline;
  zoom: 1;
  color: #333;
  font-size: 14px;
  font-weight: bold;
  margin: 0;
  padding: 0;
  line-height: normal;
}
.ui-box-foot-right {
  float: right;
}
.media-cap {
  width: 64px;
  height: 64px;
  overflow: hidden;
  float: left;
  margin-right: 15px;
}
.media-cap img {
  width: 64px;
}
.media-body-title {
  line-height: 1;
  margin-bottom: 8px;
}
.media-body-desc {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.media-label {
  line-height: 1;
  display: inline-block;
  width: 100px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.media.media-top .media-cap {
  width: 100%;
  float: none;
  margin: 0;
  border: 0;
}
.media.media-top .media-cap img {
  width: auto;
  max-width: 100%;
  vertical-align: top;
}
.media.media-top .media {
  margin: 0 15px;
}
.media.media-top .media-body {
  border: 1px solid #eee;
  padding: 15px;
}
.media.media-right .media-cap {
  float: right;
  margin-left: 15px;
  margin-right: 0;
}
.media .media-hover {
  background: #fffff5;
  padding: 0;
  border: 0;
}
.media .media-hover .media-body {
  padding: 15px;
  margin: 0 -15px;
}
.media + .media {
  border-top: 1px solid rgba(211, 214, 219, 0.5);
  margin-top: 15px;
  padding-top: 15px;
}
.media:first-child {
  border-top: 0;
  margin-top: 0;
  padding-top: 0;
}
.media + .ui-list {
  margin-top: 15px;
}
