/* --------------------------------------------------------------
 * = 列表样式
 * ----------------------------------------------------------- */
.list {
  color: #333;
  border-top: none;
  background: #fff;
}
.list li {
  position: relative;
}
.list li a {
  line-height: normal;
  display: block;
  padding: 8px 15px;
  color: #333;
  border: 1px solid #ccc;
  margin-bottom: -1px;
  transition: .3s;
}
.list li a:hover,
.list li a.active {
  text-decoration: none;
  color: #fff;
  background: #168BDE;
}
.list-round li:first-child a {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
.list-round li:last-child a {
  margin-bottom: 0;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}
.ui-list {
  margin: 0;
  list-style: square inside;
}
.ui-list li {
  line-height: 20px;
}
.ui-list li a {
  line-height: 20px;
  text-decoration: none;
  color: #005ca1;
}
.ui-list li a:hover {
  text-decoration: underline;
}
.ui-list li a,
.ui-list li span.ui-list-item-text {
  vertical-align: middle;
}
.ui-list-gray li {
  color: #333;
}
.ui-list-graylink li {
  color: #333;
}
.ui-list-graylink li a {
  color: #999;
}
.ui-list-nosquare {
  list-style: none;
}
.ui-dlist {
  color: #333;
  line-height: 2.2;
}
.ui-dlist dt {
  float: left;
  font-weight: 700;
  width: 20%;
  text-align: right;
  margin: 0;
}
.ui-dlist dd {
  float: left;
  width: 80%;
  text-align: left;
  margin: 0;
}
/* --------------------------------------------------------------
 * = 选项卡
 * ----------------------------------------------------------- */
.tab-title {
  position: relative;
  z-index: 2;
  border-left: 1px solid #ccc;
}
.tab-title li {
  float: left;
  border: 1px solid #ccc;
  border-bottom: none;
  border-left: none;
  background: #e7e7e7;
  line-height: normal;
}
.tab-title li.active {
  background: #fff;
  transition: .3s;
}
.tab-title li a {
  display: block;
  padding: 8px 15px;
  white-space: nowrap;
  color: #333;
}
.tab-content-pane {
  display: none;
  padding: 15px;
}
.ui-tab-h .tab-content {
  position: relative;
  top: -1px;
  border: 1px solid #ccc;
}
.ui-tab-h .tab-content .active {
  display: block;
}
.ui-tab-v .tab-title {
  width: 20%;
  float: left;
  border-left: none;
  border-bottom: 1px solid #ccc;
}
.ui-tab-v .tab-title li {
  float: none;
}
.ui-tab-v .tab-title li a {
  border-left: 1px solid #ccc;
}
.ui-tab-v .tab-title .active {
  border-right: 0;
}
.ui-tab-v .tab-title .active a {
  color: #333;
}
.ui-tab-v .tab-content {
  width: 80%;
  float: left;
}
.ui-tab-v .tab-content .tab-content-pane {
  border: 1px solid #ccc;
  margin-left: -1px;
}
/* --------------------------------------------------------------
 * = 简码样式
 * ----------------------------------------------------------- */
.toggle,
.accordion {
  margin-top: 1px;
}
.toggle dt,
.accordion dt,
.toggle dd,
.accordion dd {
  border: 1px solid #ccc;
}
.toggle dt,
.accordion dt {
  margin-top: -1px;
}
.toggle dt a,
.accordion dt a {
  background: #eee;
  color: #333;
  display: block;
  padding: 8px 15px;
}
.toggle dd,
.accordion dd {
  margin: 0;
  padding: 15px;
  border-top: 0;
}
/* --------------------------------------------------------------
 * = 提示信息
 * ----------------------------------------------------------- */
.alert {
  padding: 8px 15px;
  margin-bottom: 1em;
  background: #fcf8e3;
  color: #c09853;
}
.alert a {
  color: #963;
}
.alert-header {
  font-size: 18px;
  color: inherit;
  vertical-align: middle;
}
.alert-content {
  padding-left: 1.5em;
}
.alert-ok {
  background: #dff0d8;
  color: #3c763d;
}
.alert-error {
  background: #f2dede;
  color: #b94a48;
}
.alert i:before {
  vertical-align: text-top;
}
/* --------------------------------------------------------------
 * = 弹出框
 * ----------------------------------------------------------- */
.blocker {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  z-index: 1;
  padding: 20px;
  box-sizing: border-box;
  background-color: #000000;
  background-color: rgba(0, 0, 0, 0.3);
  text-align: center;
}
.blocker:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  margin-right: -0.05em;
}
.modal {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  text-align: left;
  z-index: 2;
  background: #fff;
  box-shadow: 1px 1px 2px 2px #888;
  width: 600px;
  _overflow: hidden;
}
.modal-open {
  overflow: hidden;
  overflow-y: scroll;
}
.modal-title {
  height: 42px;
  line-height: 42px;
  padding: 0 20px;
  font-size: 16px;
  border-bottom: 1px solid #ddd;
}
.modal-content {
  padding: 15px 20px;
}
.modal .close {
  position: absolute;
  top: 10px;
  right: 20px;
}
.modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(18, 18, 18, 0.3);
  z-index: 1001;
}
.modal-spinner {
  display: none;
  width: 64px;
  height: 64px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-right: -32px;
  margin-top: -32px;
  border-radius: 8px;
}
.modal-load .modal-content {
  background: transparent url(../images/loader.gif) center center no-repeat;
}
.modal .button {
  margin-right: 5px;
}
.modal a.close-modal {
  display: none;
}
/* --------------------------------------------------------------
 * = 下拉
 * ----------------------------------------------------------- */
.dropit {
  list-style: none;
  padding: 0;
  margin: 0;
}
.dropit .dropit-trigger {
  position: relative;
}
.dropit .dropit-submenu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  min-width: 150px;
  list-style: none;
  padding: 0;
  margin: 0;
  border: 1px solid #ccc;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
}
.dropit .dropit-submenu li a {
  border: 0;
}
.dropit .dropit-open .dropit-submenu {
  display: block;
}
/* --------------------------------------------------------------
 * = 分页
 * ----------------------------------------------------------- */
.ui-paginator {
  width: 100%;
  margin: 25px 0;
  padding: 0;
}
.ui-paginator li {
  display: inline-block;
  *display: inline;
  /* IE < 8: fake inline-block */
  zoom: 1;
  letter-spacing: normal;
  word-spacing: normal;
  vertical-align: top;
  text-rendering: auto;
}
.ui-paginator .pure-button {
  border-radius: 0;
  padding: 5px 12px;
  background: #e7e7e7;
  vertical-align: top;
  line-height: normal;
}
.ui-paginator .pure-button:focus,
.ui-paginator .pure-button:active {
  outline-style: none;
}
.ui-paginator .pure-button-active {
  color: #fff;
  background: #168BDE;
  box-shadow: none;
}
.ui-paginator .prev,
.ui-paginator .next {
  color: #c0c1c3;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.45);
}
.ui-paginator .prev {
  border-radius: 2px 0 0 2px;
}
.ui-paginator .next {
  border-radius: 0 2px 2px 0;
}
/* --------------------------------------------------------------
 * = 布局-bxslider
 * ----------------------------------------------------------- */
.bx-wrapper .bxslider {
  margin: 0;
  padding: 0;
}
ul.bxslider {
  list-style: none;
}
.bx-viewport {
  -webkit-transform: translatez(0);
}
.bx-wrapper {
  position: relative;
  margin: 0 auto 60px;
  padding: 0;
  *zoom: 1;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
}
.bx-wrapper img {
  max-width: 100%;
  display: block;
}
.bx-wrapper .bx-pager,
.bx-wrapper .bx-controls-auto {
  position: absolute;
  bottom: -30px;
  width: 100%;
}
.bx-wrapper .bx-loading {
  min-height: 50px;
  background: url(../images/bx_loader.gif) center center no-repeat #ffffff;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2000;
}
.bx-wrapper .bx-pager {
  text-align: center;
  font-size: .85em;
  font-family: Arial;
  font-weight: bold;
  color: #666;
  padding-top: 20px;
}
.bx-wrapper .bx-pager.bx-default-pager a {
  background: #666;
  text-indent: -9999px;
  display: block;
  width: 10px;
  height: 10px;
  margin: 0 5px;
  outline: 0;
  border-radius: 5px;
}
.bx-wrapper .bx-pager.bx-default-pager a:hover,
.bx-wrapper .bx-pager.bx-default-pager a.active,
.bx-wrapper .bx-pager.bx-default-pager a:focus {
  background: #000;
}
.bx-wrapper .bx-pager-item,
.bx-wrapper .bx-controls-auto .bx-controls-auto-item {
  display: inline-block;
  *zoom: 1;
  *display: inline;
}
.bx-wrapper .bx-pager-item {
  font-size: 0;
  line-height: 0;
}
.bx-wrapper .bx-prev {
  left: 10px;
  background: url(../images/controls.png) no-repeat 0 -32px;
}
.bx-wrapper .bx-prev:hover,
.bx-wrapper .bx-prev:focus {
  background-position: 0 0;
}
.bx-wrapper .bx-next {
  right: 10px;
  background: url(../images/controls.png) no-repeat -43px -32px;
}
.bx-wrapper .bx-next:hover,
.bx-wrapper .bx-next:focus {
  background-position: -43px 0;
}
.bx-wrapper .bx-controls-direction a {
  position: absolute;
  top: 50%;
  margin-top: -16px;
  outline: 0;
  width: 32px;
  height: 32px;
  text-indent: -9999px;
  z-index: 9999;
}
.bx-wrapper .bx-controls-direction a.disabled {
  display: none;
}
.bx-wrapper .bx-controls-auto {
  text-align: center;
}
.bx-wrapper .bx-controls-auto .bx-start {
  display: block;
  text-indent: -9999px;
  width: 10px;
  height: 11px;
  outline: 0;
  background: url(../images/controls.png) -86px -11px no-repeat;
  margin: 0 3px;
}
.bx-wrapper .bx-controls-auto .bx-start:hover,
.bx-wrapper .bx-controls-auto .bx-start.active,
.bx-wrapper .bx-controls-auto .bx-start:focus {
  background-position: -86px 0;
}
.bx-wrapper .bx-controls-auto .bx-stop {
  display: block;
  text-indent: -9999px;
  width: 9px;
  height: 11px;
  outline: 0;
  background: url(../images/controls.png) -86px -44px no-repeat;
  margin: 0 3px;
}
.bx-wrapper .bx-controls-auto .bx-stop:hover,
.bx-wrapper .bx-controls-auto .bx-stop.active,
.bx-wrapper .bx-controls-auto .bx-stop:focus {
  background-position: -86px -33px;
}
.bx-wrapper .bx-controls.bx-has-controls-auto.bx-has-pager .bx-pager {
  text-align: left;
  width: 80%;
}
.bx-wrapper .bx-controls.bx-has-controls-auto.bx-has-pager .bx-controls-auto {
  right: 0;
  width: 35px;
}
.bx-wrapper .bx-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  background: #666;
  background: rgba(80, 80, 80, 0.75);
  width: 100%;
}
.bx-wrapper .bx-caption span {
  color: #fff;
  display: block;
  padding: 10px;
}
/* --------------------------------------------------------------
 * = 布局-全局样式
 * ----------------------------------------------------------- */
.pure-g [class*="pure-u"] {
  font: 300 1em/1.8 PingFang SC, Lantinghei SC, Microsoft Yahei, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans;
}
a {
  text-decoration: none;
  color: #005ca1;
}
a:hover {
  color: #003055;
  text-decoration: none;
}
.wrap {
  max-width: 1056px;
  margin: 0 auto;
}
.row {
  margin-left: -15px;
  margin-right: -15px;
}
.row .col {
  padding-left: 15px;
  padding-right: 15px;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}
/* --------------------------------------------------------------
 * = 网站特殊样式
 * ----------------------------------------------------------- */
.demo-header {
  background: #2d3e50;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  padding: 1.5em;
  color: #fff;
  margin-bottom: 30px;
}
.demo-header h1,
.demo-header h3 {
  color: #fff;
}
.demo-footer {
  border-top: 1px solid #eee;
  padding: 1.5em;
  margin-top: 30px;
}
.sep {
  margin-bottom: 15px;
}
.pure-button {
  margin-right: 5px;
}
.animated {
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
@-webkit-keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
    -ms-transform: translateY(-20px);
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
  }
}
.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}
@-webkit-keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
    -ms-transform: translateY(20px);
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
  }
}
.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}
@-webkit-keyframes fadeInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}
@keyframes fadeInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
    -ms-transform: translateX(-20px);
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
  }
}
.fadeInLeft {
  -webkit-animation-name: fadeInLeft;
  animation-name: fadeInLeft;
}
